import { useCallback, useEffect, useState } from 'react'
import { useWallet } from 'use-wallet'
import { provider } from 'web3-core'
import { usePriceFeedContext } from "../context/PriceFeedContext"
import { getAMASTRPrice } from '../utils'

export const useAMASTRPrice = () => {

  const [amastrPrice, setAMASTRPrice] = useState<Number>(0)
  const { coinGecko } = usePriceFeedContext()
  const { ethereum }: { ethereum: provider } = useWallet()

  const fetchCirculatingSupply = useCallback(async () => {
    if (!ethereum || !coinGecko) {
      return
    }
    const price = new Number(await getAMASTRPrice(ethereum, coinGecko))
    setAMASTRPrice(price)
  }, [
    ethereum,
  ])

  useEffect(() => {
    if (ethereum) {
      fetchCirculatingSupply()
      const refreshInterval = setInterval(fetchCirculatingSupply, 270000)
      return () => clearInterval(refreshInterval)
    } else {
      return
    }
  }, [
    ethereum,
    fetchCirculatingSupply,
  ])

  return amastrPrice
}