import React from 'react'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Card from 'react-bootstrap/Card'
import StakeCard from '../components/stakeCard'
import Spinner from 'react-bootstrap/Spinner'
import { usePoolStats } from '../hooks/usePoolStats'
import { usePoolAPY } from '../hooks/usePoolAPY'
import { useTotalValueLocked } from '../hooks/useTotalValueLocked'

export default function Stake() {

  const pools = usePoolStats()
  const poolAPY = usePoolAPY()
  const totalValueLocked = useTotalValueLocked()

  return (
    <Container className="amastr-container">
      <Row className="justify-content-md-center">
        <Col xs lg="6">
          <Card text={"white"} className="rounded-lg border-0 amastr-bg-color-linear"> 
            <Card.Body>
              <Card.Title className="text-white-75 font-weight-bold">Stake (Not Live)</Card.Title>
              <Card.Text className="text-white-70 small">
                Deposit your WETH / AMASTR / Liquidity Provider tokens to receive AMASTR.
              </Card.Text>
              <Card.Text className="text-white-70 small">
                AMASTR will be the governance token of AlphaPools.Finance, hold AMASTR to receive dividends from future projects such as vault, lending, AlphaSwap and NFT's.
              </Card.Text>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="justify-content-md-center pt-4">
        <Col xs lg="3">
          <span className="text-white font-weight-bold">AMASTR Pools Not Live, see Vaults to farm APOOL</span>
        </Col>
        <Col xs lg="3" className="text-right text-white">
          {totalValueLocked ?
            <>
              <span>TVL: {Number(totalValueLocked.toFixed(2)).toLocaleString()}</span>
              <span> </span>
              <span className="small">USD</span>
            </> :
            <Spinner as="span" animation="border" size="sm" role="status" aria-hidden="true" />
          }
        </Col>
      </Row>

      {pools && pools.map((pool, e) => {
        return (
          <Row className="justify-content-md-center pt-4">
            <Col xs lg="6">
              <StakeCard pool={pool} poolAPY={poolAPY ? (poolAPY[pool?.id] ? poolAPY[pool?.id] : 0) : 0} />
            </Col>
          </Row>
        )})
      }
      
    </Container>
  )
}