import { useCallback } from "react"
import { useWallet } from "use-wallet"
import { provider } from "web3-core"
import { AlphaVaultsStake, AlphaVaultsUnStake } from "../../utils"

export const useAlphaVaultsStake = (pid: number) => {
  const {
    account,
    ethereum,
  }: { account: string | null; ethereum: provider } = useWallet()

  const handleAlphaVaultsStake = useCallback(
    async (amount: string) => {
      if (account) {
        const txHash = await AlphaVaultsStake(ethereum, pid, amount, account)
        return txHash
      }
    },
    [account, ethereum]
  )

  const handleAlphaVaultsUnStake = useCallback(
    async (amount: string) => {
      if (account) {
        const txHash = await AlphaVaultsUnStake(ethereum, pid, amount, account)
        return txHash
      }
    },
    [account, ethereum]
  )

  return { onAlphaVaultsStake: handleAlphaVaultsStake, onAlphaVaultsUnStake: handleAlphaVaultsUnStake }
}