import React from 'react'
import { useParams } from 'react-router-dom'
import { useAlphaVaultsStats } from '../hooks/alphapools/useAlphaVaultsStats'
import AlphaPoolsFarmPanel from '../components/AlphaPoolsFarmPanel'

interface ParamTypes {
  path: string
}

export default function AlphaVaultsPool() {
  const { path } = useParams<ParamTypes>()

  const pools = useAlphaVaultsStats()
  const pool = pools.find((e, i) => {return (e.path === path)})

  if (path && pools.length > 0 && pool) {
    return (
      <AlphaPoolsFarmPanel pool={pool} />
    )
  } else {
    return (<></>)
  }
}