const getWeeklyRewards = async function (synthContract) {
  const rewardRate = await synthContract.methods.initreward().call()
  return bnToDec(new BN(rewardRate))
}

interface PoolAPY {
  ethAMASTRPoolAPY: number | null
  wethPoolAPY: number | null
  amastrPoolAPY: number | null
}

export const getPoolAPY = async (provider: provider): Promise<PoolAPY | null> => {
  if (provider) {
    try {
      const ethAMASTRPoolContract = getPoolContract(provider, ETH_AMASTR_UNI_LP_POOL_ADDRESS)
      const amastrPoolContract = getPoolContract(provider, AMASTR_POOL_ADDRESS)
      const wethPoolContract = getPoolContract(provider, WETH_POOL_ADDRESS)

      const ethAMASTRPoolRate = await getWeeklyRewards(ethAMASTRPoolContract)
      const amastrPoolRate = await getWeeklyRewards(amastrPoolContract)
      const wethPoolRate = await getWeeklyRewards(wethPoolContract)

      const amastrTokenContract = getERC20Contract(provider, AMASTR_TOKEN_ADDRESS)
      const wethContract = getERC20Contract(provider, WETH_TOKEN_ADDRESS)
      const ethAMASTRLPContract = getERC20Contract(provider, ETH_AMASTR_UNI_LP_TOKEN_ADDRESS)

      const totalAMASTRInUniswap = (await amastrTokenContract.methods.balanceOf(ETH_AMASTR_UNI_LP_TOKEN_ADDRESS).call()) / 1e18
      const totalWETHInUniswap = (await wethContract.methods.balanceOf(ETH_AMASTR_UNI_LP_TOKEN_ADDRESS).call()) / 1e18
      const totalETHAMASTRLPStaked = (await ethAMASTRPoolContract.methods.totalSupply().call()) / 1e18
      const totalETHAMASTRLP = (await ethAMASTRLPContract.methods.totalSupply().call()) / 1e18
      const totalWETHStaked = (await wethPoolContract.methods.totalSupply().call()) / 1e18
      const totalAMASTRStaked = (await amastrPoolContract.methods.totalSupply().call()) / 1e18

      const ethAMASTRPoolAPY = ethAMASTRPoolRate / 7.0 * 365.0 / (totalETHAMASTRLPStaked / totalETHAMASTRLP * totalAMASTRInUniswap * 2)
      const wethPoolAPY = wethPoolRate / 7.0 * 365 / (totalWETHStaked * totalAMASTRInUniswap / totalWETHInUniswap)
      const amastrPoolAPY = amastrPoolRate / 7.0 * 365 / (totalAMASTRStaked)

      return {
        ethAMASTRPoolAPY,
        wethPoolAPY,
        amastrPoolAPY
      }
    } catch (e) {
      console.log(e)
      return null
    }
  } else {
    return null
  }
}

export const getTotalValueLocked = async (provider: provider, coinGecko: any): Promise<number> => {
  if (provider && coinGecko) {
    try {
      const { data } = await coinGecko.simple.fetchTokenPrice({
        contract_addresses: WETH_TOKEN_ADDRESS,
        vs_currencies: "usd",
      })

      const wethPoolContract = getPoolContract(provider, WETH_POOL_ADDRESS)
      const amastrPoolContract = getPoolContract(provider, AMASTR_POOL_ADDRESS)

      const amastrTokenContract = getERC20Contract(provider, AMASTR_TOKEN_ADDRESS)
      const wethContract = getERC20Contract(provider, WETH_TOKEN_ADDRESS)

      const totalAMASTRInUniswap = (await amastrTokenContract.methods.balanceOf(ETH_AMASTR_UNI_LP_TOKEN_ADDRESS).call()) / 1e18
      const totalWETHInUniswap = (await wethContract.methods.balanceOf(ETH_AMASTR_UNI_LP_TOKEN_ADDRESS).call()) / 1e18
      const totalWETHStaked = (await wethPoolContract.methods.totalSupply().call()) / 1e18
      const totalAMASTRStaked = (await amastrPoolContract.methods.totalSupply().call()) / 1e18

      const totalValueLockedInUniswap = totalWETHInUniswap * 2 * data[WETH_TOKEN_ADDRESS].usd
      const totalValueLockedInWETHPool = totalWETHStaked * data[WETH_TOKEN_ADDRESS].usd
      const totalValueLockedInAMASTRPool = totalAMASTRStaked * totalWETHInUniswap / totalAMASTRInUniswap * data[WETH_TOKEN_ADDRESS].usd

      return totalValueLockedInUniswap + totalValueLockedInWETHPool + totalValueLockedInAMASTRPool
    } catch (e) {
      console.log(e)
      return 0
    }
  } else {
    return 0
  }
}

export const getCirculatingSupply = async (provider: provider): Promise<number> => {
  if (provider) {
    try {
      const amastrTokenContract = getERC20Contract(provider, AMASTR_TOKEN_ADDRESS)

      const ethAMASTRPoolContract = getPoolContract(provider, ETH_AMASTR_UNI_LP_POOL_ADDRESS)
      const amastrPoolContract = getPoolContract(provider, AMASTR_POOL_ADDRESS)
      const wethPoolContract = getPoolContract(provider, WETH_POOL_ADDRESS)

      const totalAMASTRMinted = (await amastrTokenContract.methods.totalSupply().call()) / 1e18 - 1500

      const ethAMASTRPoolRewardRate = await ethAMASTRPoolContract.methods.rewardRate().call() / 1e18
      const amastrPoolRewardRate = await amastrPoolContract.methods.rewardRate().call() / 1e18
      const wethPoolRewardRate = await wethPoolContract.methods.rewardRate().call() / 1e18

      const ethAMASTRPeriodFinish = await ethAMASTRPoolContract.methods.periodFinish().call()
      const amastrPoolPeriodFinish = await amastrPoolContract.methods.periodFinish().call()
      const wethPoolPeriodFinish = await wethPoolContract.methods.periodFinish().call()

      const currentTimestamp = new Date().getTime() / 1000 

      const ethAMASTRPoolRemainAmount = currentTimestamp <= ethAMASTRPeriodFinish ? (ethAMASTRPeriodFinish - currentTimestamp) * ethAMASTRPoolRewardRate : 0
      const amastrPoolRemainAmount = currentTimestamp <= amastrPoolPeriodFinish ? (amastrPoolPeriodFinish - currentTimestamp) * amastrPoolRewardRate : 0
      const wethPoolRemainAmount = currentTimestamp <= wethPoolPeriodFinish ? (wethPoolPeriodFinish - currentTimestamp) * wethPoolRewardRate : 0

      const ethAMASTRPoolDevFund = await ethAMASTRPoolContract.methods.devWithdrawnAmt().call() / 1e18
      const wethPoolDevFund = await wethPoolContract.methods.devWithdrawnAmt().call() / 1e18
      const amastrPoolDevFund = await amastrPoolContract.methods.devWithdrawnAmt().call() / 1e18

      return totalAMASTRMinted - ethAMASTRPoolRemainAmount - amastrPoolRemainAmount - wethPoolRemainAmount + ethAMASTRPoolDevFund + wethPoolDevFund + amastrPoolDevFund
    } catch (e) {
      console.log(e)
      return 0
    }
  } else {
    return 0
  }
}

export const getAMASTRPrice = async (provider: provider, coinGecko: any) : Promise<number> => {
  if (provider && coinGecko) {
    try {
      const { data } = await coinGecko.simple.fetchTokenPrice({
        contract_addresses: WETH_TOKEN_ADDRESS,
        vs_currencies: "usd",
      })
      const amastrTokenContract = getERC20Contract(provider, AMASTR_TOKEN_ADDRESS)
      const wethContract = getERC20Contract(provider, WETH_TOKEN_ADDRESS)

      const totalAMASTRInUniswap = (await amastrTokenContract.methods.balanceOf(ETH_AMASTR_UNI_LP_TOKEN_ADDRESS).call()) / 1e18
      const totalWETHInUniswap = (await wethContract.methods.balanceOf(ETH_AMASTR_UNI_LP_TOKEN_ADDRESS).call()) / 1e18

      return totalWETHInUniswap / totalAMASTRInUniswap * data[WETH_TOKEN_ADDRESS].usd
    } catch (e) {
      console.log(e)
      return 0
    }
  } else {
    return 0
  }
}

interface AlphaPoolsLGEStats {
  liquidityGenerationOngoing: boolean
  totalETHContributed: number
  totalAMASTRContributed: number
  alphapoolsPriceEstimateAfterLGE: number
  alphapoolsMarketCapEstimateAfterLGE: number
  userETHContributed: number
  userAMASTRContributed: number
  lgeParticipationAgreement: string
  secondsLeftInLiquidityGenerationEvent: number
  lpPerETHUnit: number
  lpPerAMASTRUnit: number
  alphapoolsForAMASTRAllocate: number
}

export const getAPOOLLGEStats = async (provider: provider, coinGecko: any, account: string | null) : Promise<AlphaPoolsLGEStats | null> => {
  if (provider && coinGecko && account) {
    try {
      const { data } = await coinGecko.simple.fetchTokenPrice({
        contract_addresses: [WETH_TOKEN_ADDRESS, AMASTR_TOKEN_ADDRESS],
        vs_currencies: "usd",
      })

      const alphapoolsLGEContract = getAlphaPoolsLGEContract(provider, APOOL_LGE_ADDRESS)

      const liquidityGenerationOngoing = await alphapoolsLGEContract.methods.liquidityGenerationOngoing().call()
      const totalETHContributed = await alphapoolsLGEContract.methods.totalETHContributed().call() / 1e18
      const totalAMASTRContributed = await alphapoolsLGEContract.methods.totalAMASTRContributed().call() / 1e18
      const userETHContributed = await alphapoolsLGEContract.methods.ethContributed(account).call() / 1e18
      const userAMASTRContributed = await alphapoolsLGEContract.methods.amastrContributed(account).call() / 1e18
      const lgeParticipationAgreement = await alphapoolsLGEContract.methods.liquidityGenerationParticipationAgreement().call()
      const alphapoolsPriceEstimateAfterLGE = (totalETHContributed * data[WETH_TOKEN_ADDRESS].usd + totalAMASTRContributed * data[AMASTR_TOKEN_ADDRESS].usd) / 270000
      const alphapoolsMarketCapEstimateAfterLGE = alphapoolsPriceEstimateAfterLGE * 270000
      const lpPerETHUnit = await alphapoolsLGEContract.methods.LPperETHUnit().call() / 1e18
      const lpPerAMASTRUnit = await alphapoolsLGEContract.methods.LPperAMASTRUnit().call() / 1e18
      const alphapoolsForAMASTRAllocate = await alphapoolsLGEContract.methods.calculateAMASTRAllocate().call() / 1e18

      let secondsLeftInLiquidityGenerationEvent
      try {
        secondsLeftInLiquidityGenerationEvent = await alphapoolsLGEContract.methods.getSecondsLeftInLiquidityGenerationEvent().call()
      } catch (e) {
        secondsLeftInLiquidityGenerationEvent = 0
      }

      return {
        liquidityGenerationOngoing,
        totalETHContributed,
        totalAMASTRContributed,
        alphapoolsPriceEstimateAfterLGE,
        alphapoolsMarketCapEstimateAfterLGE,
        userETHContributed,
        userAMASTRContributed,
        lgeParticipationAgreement,
        secondsLeftInLiquidityGenerationEvent,
        lpPerETHUnit,
        lpPerAMASTRUnit,
        alphapoolsForAMASTRAllocate
      }
    } catch (e) {
      console.log(e)
      return null
    }
  } else {
    return null
  }
}

