interface AlphaVaultsStats {
  totalLPStaked: number
  userLPStaked: BN
  poolAPY: number
  userClaimableAPOOL: number
}

export const getAlphaPoolsAlphaVaultsStats = async (provider: provider, pid: number, account: string | null) : Promise<AlphaVaultsStats | null> => {
  if (provider && pid != undefined && account) {
    try {
      const alphapoolsTokenContract = getNBUNIERC20Contract(provider, APOOL_TOKEN_ADDRESS)
      const AlphaVaultsContract = getAlphaVaultsContract(provider, ALPHAVAULTS_ADDRESS)

      const poolInfo = await AlphaVaultsContract.methods.poolInfo(pid).call()
      const lpTokenContract = getERC20Contract(provider, poolInfo.token)

      const epochCalculationStartBlock = await AlphaVaultsContract.methods.epochCalculationStartBlock().call()

      const web3 = new Web3(provider)
      const block = web3.eth.getBlock(epochCalculationStartBlock)
      const epochCalculationStartTimestap = Number((await block).timestamp)

      const rewardsInThisEpoch = await AlphaVaultsContract.methods.rewardsInThisEpoch().call() / 1e18

      const totalLPSupply = await lpTokenContract.methods.totalSupply().call() / 1e18
      const totalLPStaked = await lpTokenContract.methods.balanceOf(ALPHAVAULTS_ADDRESS).call() / 1e18
      const totalAPOOLInUniswap = (await alphapoolsTokenContract.methods.balanceOf(poolInfo.token).call()) / 1e18

      const userLPStaked = (await AlphaVaultsContract.methods.userInfo(pid, account).call()).amount

      const totalRewardFullYear = rewardsInThisEpoch / ((Date.now() / 1000) - Number(epochCalculationStartTimestap)) * 24 * 3600 * 365

      const poolAPY = totalRewardFullYear * Number(poolInfo.allocPoint) / APOOL_ALPHAVAULTS_TOTAL_POINT / ((totalLPStaked / totalLPSupply) * totalAPOOLInUniswap * 2)

      const userClaimableAPOOL = await AlphaVaultsContract.methods.pendingAlphaPools(pid, account).call() / 1e18

      return {
        totalLPStaked,
        userLPStaked,
        poolAPY,
        userClaimableAPOOL
      }
    } catch (e) {
      console.log(e)
      return null
    }
  } else {
    return null
  }
}

