import { useCallback } from "react"
import { useWallet } from "use-wallet"
import { provider } from "web3-core"
import { AlphaVaultsClaim } from "../../utils"

export const useAlphaVaultsClaim = (pid: number) => {
  const {
    account,
    ethereum,
  }: { account: string | null; ethereum: provider } = useWallet()

  const handleAlphaVaultsClaim = useCallback(async () => {
    if (account) {
      const txHash = await AlphaVaultsClaim(ethereum, pid, account)
      return txHash
    }
  }, [account, ethereum])

  return { onAlphaVaultsClaim: handleAlphaVaultsClaim }
}
