import {
  useState,
  useEffect,
  useCallback,
} from "react"
import BN from "bignumber.js"
import { useWallet } from "use-wallet"
import { getAlphaPoolsAlphaVaultsStats } from '../../utils'
import { provider } from "web3-core"
import { APOOL_FARM_POOLS } from '../../constants/alphapoolsFarmPool'

export interface IAlphaVaultsStats {
  id: number
  path: string
  name: string
  logos: string[]
  tokenName: string
  tokenAddress: string
  totalLPStaked: number | undefined
  userLPStaked: BN | null
  poolAPY: number | undefined
  userClaimableAPOOL: number | undefined
}

export const useAlphaVaultsStats = () => {  
  const [AlphaVaultsStatus, setAlphaVaultsStats] = useState<IAlphaVaultsStats[]>([])

  const {
    ethereum,
    account,
  }: { ethereum: provider; account: string | null } = useWallet()

  const getAlphaVaultsStats = useCallback(async () => {
    const promisedAlphaVaultsStatsArr = APOOL_FARM_POOLS.map(async (pool) => {
      const _AlphaVaultsStats = await getAlphaPoolsAlphaVaultsStats(
        ethereum,
        pool.id,
        account
      )
      return {
        id: pool.id,
        path: pool.path,
        name: pool.name,
        logos: pool.logos,
        tokenName: pool.tokenName,
        tokenAddress: pool.tokenAddress,
        totalLPStaked: _AlphaVaultsStats?.totalLPStaked,
        userLPStaked: _AlphaVaultsStats?.userLPStaked ? new BN(_AlphaVaultsStats?.userLPStaked) : null,
        poolAPY: _AlphaVaultsStats?.poolAPY,
        userClaimableAPOOL: _AlphaVaultsStats?.userClaimableAPOOL,
      }
    })

    const resolvedAlphaVaultsStatsArr = await Promise.all(promisedAlphaVaultsStatsArr)
    setAlphaVaultsStats(resolvedAlphaVaultsStatsArr)
  }, [ethereum, account])

  useEffect(() => {
    getAlphaVaultsStats()
    const refreshInterval = setInterval(getAlphaVaultsStats, 270000)
    return () => clearInterval(refreshInterval)
  }, [getAlphaVaultsStats])

  return AlphaVaultsStatus
}